.app {
  background-color: blue;
  background-size: cover;
  height: 100%;
  text-align: center;
}

.app-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.app-header {
  background: url('../images/minerFinder-background.png');
  background-size: cover;
  display: flex;
  height: 100vh;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding-bottom: 5vh;
  opacity: .7;
  overflow-x: hidden;
}

.app-link {
  color: #61dafb;
}

.information-bar{
  display: flex;
  justify-content: space-around;
  width: 78%;
}

@media screen and (max-width: 1100px) {
  .information-bar{
    width: 100%;
  }
}
